body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root, .App, .page_container{
  height: 100%;
  width: 100%;
  margin: 0;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.main-container {
	display: flex;
	height: calc(100vh - 50px);
	background: url(/static/media/medicine.526d9427.jpg) no-repeat;
	background-size: 100%;
}

.text-block {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background-color: white;
	height: 100%;
	line-height: 220%;
	font-size: 15px;
	padding-left: 5%;
	padding-right: 5%;
	text-align: left;
	min-width: 40%;
	max-width: 700px;
	overflow: auto;
}

.text-block > h1 {
	display: flex;
	align-items: center;
	font-weight: bold;
	margin-bottom: 40px;
}

.institutions-list, .institutions-container {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
}

.input-fields {
	display: flex;
	width: 100%;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}
@media screen and (max-width: 736px) {
	.text-block{
		font-size: 14px;
	}
	.text-block > h1 {
		margin-top: -10%;
	}
	.input-fields input{
		height: 35px;
	}
	.input-fields button{
		height: 35px;
	}

}

@media screen and (max-width: 480px) {
	.text-block{
		font-size: 12px;
	}
	.text-block > h1 {
		margin-top: -15%;
	}
	.input-fields input{
		height: 28px;
	}
	.input-fields button{
		height: 28px;
	}
	
}
.single-view-container {
	padding: 10px 5%;
	background: #e8edf2;
	min-height: 100vh;
}

.medicine-header, .meidcine-row, .place-info {
	background: white;
	display: flex;
	justify-content: space-around;
}
.medicine-header {
	margin-top: 10px;
}
.medicine-header > * {
	display: flex;
	flex-basis: 10%;
	font-size: 16px;
	font-weight: bold;
}

.place-medicine {
	background: white;
	border-radius: 5px; 
	padding-top: 10px;
	padding-left: 3%;
	padding-right: 3%;
}

.meidcine-row {
	border-bottom: 1px solid lightgray;
}

.meidcine-row > *{
	background: white;
	display: flex;
	padding: 10px 0px;
	margin: 0;
	flex-basis: 10%;
	font-size: 16px;
}

.meidcine-row > .name {
	color: #11a8ff;
	font-weight: bold;
}

.meidcine-row > .code {
	color: lightgrey;
}

.meidcine-row > .form {
	color: lightgrey;
}

.place-info {
	display: flex;
	border-radius: 5px;
	padding: 10px 5%;
	margin-bottom: 20px;
	justify-content: space-between;
}

.place-info > div{
	text-align: left;
	font-weight: bold;
	width: 20%;
}
@media screen and (max-width: 736px) {
	.place-info > div p{
		font-size: 12px;
	}
	.place-medicine input{
		height: 35px;
	}
	.place-medicine button{
		height: 35px;
	}
	.meidcine-row > *{
		font-size: 12px;
	}
	.medicine-header > * {
		display: flex;
		flex-basis: 10%;
		font-size: 8px;
		font-weight: bold;
	}

}

@media screen and (max-width: 480px) {
	.place-info > div p{
		font-size: 10px;
	}
	.place-medicine input{
		height: 25px;
	}
	.place-medicine button{
		height: 25px;
	}
	.meidcine-row > *{
		font-size: 9px;
	}
	.medicine-header > * {
		display: flex;
		flex-basis: 10%;
		font-size: 5px;
		font-weight: bold;
	}
	.right-info button{
		width: 200%;
		font-size: 10px;
		margin-left: -80%;
	}
}
.navbar {
	display: flex;
	flex-direction: row-reverse;
	background: #151e2d;
}

.title {
	margin: 0px;
	font-size: 32px;
	font-weight: bold;
}

.title {
	color: #49b6ff;
}

.title > span {
	color: #68ff3a;
}

